<template>
  <div>
    <div class="top-title"> </div>
    <div class="add-box">
      <p class="add-title"></p>
      <div>
        <Form ref="formCustom" :model="formCustom" :rules="ruleValidate" :label-width="100"  label-position="left"class="form">
          <FormItem label=":" prop="address" >
             <v-distpicker :placeholders="placeholders" :province="select.province" :city="select.city" :area="select.area" @selected="onSelected"></v-distpicker>
              <!-- <Input  v-model="formCustom.address" placeholder="///" ></Input> -->
          </FormItem>
          <FormItem label=""prop="detail">
              <Input  v-model="formCustom.detail" placeholder=""></Input>
          </FormItem>
          <FormItem label=":" prop="postcode" >
              <Input v-model="formCustom.postcode" placeholder=""></Input>
          </FormItem>
          <FormItem label=":" prop="receName" >
              <Input v-model="formCustom.receName" placeholder="25"></Input>
          </FormItem>
          <FormItem label=":" prop="recePhone" >
              <Input v-model="formCustom.recePhone" placeholder=""></Input>
          </FormItem>
          <!-- <Radio v-model="formCustom.status" @on-change="change"></Radio> -->
          <i-switch v-model="formCustom.status" size="small"/>
      </Form>
      </div>

      <div class="btn">
        <Button type="primary" @click="handleSubmit('formCustom')" ></Button>
        <Button type="default" @click="handleReset('formCustom')"></Button>
      </div>

      <div class="address-list">
        <p></p>
      </div>
      <!--  -->
      <!-- columns12   columns4   @on-selection-change="selectChange"   @on-row-click="rowClick" @on-cell-click="cellClick"-->
      <div class="table-list">
        <i-table border ref="selection" :columns="columns4" :data="addressList" @on-select="handleSelectRow()" @on-selection-change="selectChange" >
          <template slot-scope="{ row, index }" slot="action">
            <Button type="primary" size="small" style="margin-right: 5px" @click="update(index)"></Button>
            <Button type="error" size="small" @click="remove(index)"></Button>
          </template>

           <!-- <template slot-scope="{ row, index }" slot="action1">
            <Button type="default" size="small" style="margin-right: 5px" @click="show(index)"></p></Button>
          </template> -->
        </i-table>
      </div>
       <!-- <Button @click="handleSelectAll(true)">Set all selected</Button> -->
       <Button type="error" size="small" @click="delByBatch" :disabled='multipleSelection.length===0'></Button>

    </div>
    <Modal
        v-model="modal5"
        title=""
        width="600"
        >
        <Form ref="updateForm" :model="updateForm" :rules="ruleValidate" :label-width="100"  label-position="left"class="form">
          <FormItem label=":" prop="address" >
            <Input  v-model="updateForm.address" placeholder="///" ></Input>
          </FormItem>

          <FormItem label=":" class="picker">
             <v-distpicker :placeholders="placeholders" @selected="updateSelect"></v-distpicker>
          </FormItem>

          <FormItem label=""prop="detail">
              <Input  v-model="updateForm.detail" placeholder=""></Input>
          </FormItem>
          <FormItem label=":" prop="postcode" >
              <Input v-model="updateForm.postcode" placeholder=""></Input>
          </FormItem>
          <FormItem label=":" prop="receName" >
              <Input v-model="updateForm.receName" placeholder="25"></Input>
          </FormItem>
          <FormItem label=":" prop="recePhone" >
              <Input v-model="updateForm.recePhone" placeholder=""></Input>
          </FormItem>
      </Form>
      <div slot="footer">
        <Button type="text" size="large" @click="modalCancel"></Button>
        <Button type="primary" size="large" @click="modalOk"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import VDistpicker from 'v-distpicker'
import { sendServer } from '../../utils/common'
export default {
  components:{
    VDistpicker
  },
  data(){
    const validatePhone = (rule, value, callback) => {
      let myreg=/^[1][3,4,5,7,8][0-9]{9}$/;
        if (value === '') {
            callback(new Error('11'));
        }else if(!myreg.test(value)){
            callback(new Error(''));
        }else{
            callback()
        }
    };
    return{
      multipleSelection:[],
      ids:[],
      userInfo:JSON.parse(localStorage.getItem('userdata')),
      placeholders: {
          province: '-------  --------',
          city: '---  ---',
          area: '---  ---',
      },
      select:{
        province:'',
        city:'',
        area:''
      },
      addressList:[],
      formCustom:{
        address:'',
        detail:'',
        postcode:'',
        receName:'',
        recePhone:'',
        status:false
      },
      ruleValidate: {
          address: [
              { required: true, message: '', trigger: 'blur' }
          ],
          detail: [
              { required: true, message: '', trigger: 'blur' },
          ],
          postcode: [
              { required: true, message: '', trigger: 'blur' }
          ],
          receName: [
              { required: true, message: '', trigger: 'blur' }
          ],
          recePhone: [
              { required: true, message: '', trigger: 'blur' },
              { validator: validatePhone, trigger: 'blur' }
          ]

      },
      //columns  type: 'selection'
      columns4:[
        {
            type: 'selection',
            width: 50,
            align: 'center'
        },
        {
            title: '',
            key: 'receName'
        },
        {
            title: '',
            key: 'address',
            width: 150,
        },
        {
            title: '',
            key: 'detail',
            width: 100,
        },
        {
            title: '',
            key: 'postcode',
            width: 80,
        },
         {
            title: '',
            key: 'recePhone',
        },
        {
            title: '',
            slot: 'action',
            width: 150,
            align: 'center'
        },
        {
            title: '',
            key:'moren',
            width: 90,
            render: (h, params) => {
              return h('div', [
                  h('strong', {
                    on:{
                      click: () => {
                        this.handleShow(params.row)      //
                      }
                    }
                  },params.row.moren),

              ]);
          }
        },

      ],
      modal5:false,
      updateForm:{
        address:'',
        detail:'',
        postcode:'',
        receName:'',
        recePhone:'',
        status:'',
        id:'',
      },

    }
  },
  created(){
    this.getAddList()
  },
  methods:{
    modalCancel(){
      this.modal5=false
    },
    //
    update(index){
      this.modal5=!this.modal5
      let add=this.addressList[index]
      this.updateForm.address=add.address
      this.updateForm.detail=add.detail
      this.updateForm.postcode=add.postcode
      this.updateForm.receName=add.receName
      this.updateForm.recePhone=add.recePhone
      this.updateForm.id=add.id
      this.updateForm.status=add.status

    },
    modalOk(){
      let updateForm=this.updateForm
      console.log(updateForm)
      let params={},send={}
      send.id=updateForm.id;
      send.status=updateForm.status;
      send.address=updateForm.address
      send.detail=updateForm.detail
      send.postcode=updateForm.postcode
      send.receName=updateForm.receName
      send.recePhone=updateForm.recePhone
      params.send=send;
      params.url="address/update"
      console.log(params)
      sendServer(params,(commRes)=>{
        if(commRes.code==100){
          this.getAddList()
          this.$Message.success('')
        }else{
          this.$Message.error('')
        }
      })
      this.modal5=false
    },
    handleShow(data){
      console.log(data)
      console.log(data)
      let id=data.id
      let userId=this.userInfo.id
      let params={},send={}
      send.userId=userId;
      send.id=id;
      params.send=send;
      params.url="address/setDeafult"
      sendServer(params,(commRes)=>{
        if(commRes.code==100){
          this.$Message.success("")
           this.getAddList()
        }
      })
    },
    selectChange(selection){
      // let ids=[];
      // selection.forEach(item=>{
      //  ids.push(item.id)
      // })
      // console.log(ids)
      // return ids;
      this.multipleSelection = selection;
    },
    handleSelectRow () {
      let ids = [];
      this.$refs['selection'].getSelection().map(item => {
        ids.push(item.id)
      })
      console.log(ids)
      return ids
    },
    //
    delByBatch(){
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        var ids=this.handleSelectRow()
        console.log(ids)
        this.axios({
          method:"GET",
          url:'http://localhost:8089/hf/address/deleteByBatch?ids='+ids
        }).then((res)=>{
          console.log(res)
          if(res.data.code==100){
            //
            this.getAddList()
            this.$Message.success("")
          }else{
            this.$Message.error("")
          }
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    remove (index) {
      // console.log(this.addressList[index])
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let id=this.addressList[index].id;
        let params={},send={}
        send.id=id;
        params.send=send;
        params.url="address/delete"
        sendServer(params,(commRes)=>{
          if(commRes.code==100){
            this.$Message.success("")
            this.addressList.splice(index, 1);
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    //
    getAddList(){
      let params={},send={}
      send.userId=this.userInfo.id;
      params.send=send;
      params.url='address/showAddress'
      sendServer(params,(commRes)=>{
        if(commRes.code==100){
          console.log(commRes)
          commRes.extend.addressList.forEach(item=>{
            if(item.status=='1'){
              item.moren=''
            }else{
              item.moren=''
            }
          })
          this.addressList=commRes.extend.addressList
          console.log(this.addressList)
        }
      })

    },

   updateSelect(data){
      let province=data.province.value,
      city=data.city.value,
      area=data.area.value
      let address=province+city+area
      this.updateForm.address=address
   },
    onSelected(data) {
        //  console.log(data)
        let province=data.province.value,
        city=data.city.value,
        area=data.area.value
        let address=province+city+area
        console.log(address)
        this.formCustom.address=address
    },
    handleSubmit (name) {
      this.$refs[name].validate((valid) => {
          if (valid) {
            let params={},send={};
            let status=0
            let formCustom=this.formCustom;
            send.userId=this.userInfo.id;
            send.address=formCustom.address
            send.detail=formCustom.detail
            send.postcode=formCustom.postcode
            send.receName=formCustom.receName
            send.recePhone=formCustom.recePhone
            //1 
            if(formCustom.status){
              status=1
            }
            send.status=status
            console.log(send)
            params.send=send;
            params.url="address/addAddress"
            console.log(params)
            sendServer(params,(commRes)=>{
              if(commRes.code==100){
                  this.getAddList()
                  this.$Message.success('!');
                  this.formCustom={}
                  this.select.province='-------  --------',
                  this.select.city='---  ---',
                  this.select.area='---  ---'
              }else{
                  this.$Message.error('');
              }
            })
          } else {
            this.$Message.error('');
          }
      })
    },
    handleReset (name) {
        this.$refs[name].resetFields();
    }
  }

}
</script>
<style>
.picker{
  padding-left: 10px;
}
.ivu-table-cell {
    padding-left: 18px;
    padding-right: 18px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: normal;
    word-break: break-all;
    box-sizing: border-box;
    font-size: 10px;
}
</style>
<style scoped>

.table-list{
  font-size: 12px;
  margin-bottom: 10px;
}
.address-list p{
  line-height: 30px;
  font-weight: 600;
  margin-left: 10px;
}
.address-list{
  width:100%;
  height: 30px;
  background-color: rgb(175, 207, 248);
  margin-bottom: 10px;
}

.btn{
  margin-top: 10px;
  margin-bottom: 20px;
}
.form{
  margin-top: 30px;
  width: 500px;
}
.top-title{
  margin-top: 10px;
  margin-left: 20px;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 10px;
}
.add-box{
  margin-top: 10px;
  margin-left: 20px;
  margin-bottom: 10px;
}
.add-title{
  color:#d4313f;
  font-size: 13px;
}


</style>



